// Here you can create and customize the pages' header (Navigation):

@use "../globals" as *;
@use "../helpers" as *;
@use "../mixins" as *;

%header {
  border-bottom: 1px solid black;
  background: radial-gradient(#fff, #ffd6d6);
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    min-height: 60px;
    > a {
      display: block;
      width: 125px;
    }
    nav {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      column-gap: 15px;
      ul {
        display: none;
        position: absolute;
        top: calc(100% + 2px);
        left: 0;
        width: 100%;
        @include breakpoint(tablet) {
          display: flex !important;
          position: relative;
        }
        li {
          text-align: center;
          @include breakpoint(tablet) {
            margin: 0 10px;
          }
          a {
            display: block;
            padding: 5px 0;
            border-bottom: 1px solid black;
            border-radius: 5px;
            transition: $transition;
            color: black;
            background: radial-gradient(#ff9f9f, #ffd6d6);
            &:hover {
              font-weight: bold;
              color: white;
              background: radial-gradient(#d74848, #d76464);
            }
            @include breakpoint(tablet) {
              padding: 5px 10px;
              border-bottom: none;
              background: unset;
              &:hover {
                font-weight: unset;
              }
            }
          }
        }
      }
      > a:last-of-type {
        display: block;
        width: 30px;
      }
      .toggle_menu {
        width: 30px;
        transition: $transition;
        cursor: pointer;
        @include breakpoint(tablet) {
          display: none;
        }
        span {
          width: 100%;
          display: block;
          height: 3px;
          background-color: black;
          margin: 5px auto;
        }
        span {
          transition: $transition;
        }
      }
    }
  }
}
