@use "../helpers" as *;

// This is a small lib For buttons:

// 1- >> orange button:
%btn {
  display: inline-block;
  padding: 8px 30px;
  margin: 30px 0;
  text-transform: capitalize;
  border-radius: 30px;
  color: #fff;
  transition: $transition;
  background-color: #ff523b;

  &:hover,
  &:focus {
    background: #563434;
  }
}

