// Here You can style and customize the home page:

@use "../globals" as *;
@use "../mixins" as *;
@use "../helpers" as *;
@use "../components" as *;

// <> How to organize the CSS properties:
// 1 - >> display stuff:
// 2 - >> positioning stuff:
// 3 - >> box-model stuff:
// 4 - >> typography:
// 5 - >> manipulation:
// 6 - >> misc:

// ------- start header Section -------//
header {
  @extend %header;
}
/* -------- End header Section --------*/
/* ------- Start Landing Section ------*/
.landing {
  padding: 50px 0;
  background: radial-gradient(#fff, #ffd6d6);
  .container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    text-align: center;
    height: 100%;
    @include breakpoint(laptop) {
      flex-direction: row;
      column-gap: 50px;
      min-height: calc(100vh - 68px);
    }
  }
  .intro-text {
    max-width: 555px;
    h1 {
      font-size: 35px;
      margin: 0 auto;
      text-transform: capitalize;
    }
    p {
      margin: 15px 0;
      color: $txt-clr;
    }
    a {
      @extend %btn;
    }
  }
  .img-holder {
    flex-basis: 50%;
    max-width: 450px;
    @include breakpoint(laptop) {
      width: unset;
    }
  }
}
/* -------- End Landing Section -------*/
/* ----- Start categories Section -----*/
.categories {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 50px;
    .img-holder {
      position: relative;
      transition: 1s;
      overflow: hidden;
      box-shadow: $box-shadow;
      border-radius: $border-radius;
      &::before,
      &::after {
        content: "";
        position: absolute;
        width: 2px;
        height: 0;
        transition: 1s;
        background-color: $main-clr-alt;
      }
      &::before {
        top: 0;
        left: 0;
      }
      &::after {
        bottom: 0;
        right: 0;
      }
      &:hover::before,
      &:hover::after {
        height: 100%;
      }
      &:hover {
        transform: translateY(-10px);
        box-shadow: $box-shadow-2;
      }
    }
  }
}
/* ------ End categories Section ------*/
.main-title {
  @extend %section_header-4;
}
/* ------- Start Features section -----*/
.features {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 50px;
  }
  figure {
    position: relative;
    overflow: hidden;
    border-radius: $border-radius;
    a {
      span {
        position: relative;
        display: block;
        height: 38px;
        padding: 5px;
        font-size: 18px;
        text-align: center;
        color: $white;
        z-index: 2;
        transition: $transition;
        background-color: $main-clr;
        &::before {
          content: "";
          position: absolute;
          width: 0;
          height: 100%;
          left: 0;
          top: 0;
          z-index: -1;
          transition: $transition;
          transition-delay: width 0.5s; // not woring is it should!
          background-color: $main-clr-alt;
        }
      }
    }
    figcaption {
      display: none;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      row-gap: 10px;
      position: absolute;
      text-align: center;
      height: 100%;
      width: 100%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: $transition;
      &::before {
        content: "";
        position: absolute;
        width: 0;
        height: calc(100% - 37px);
        left: 0;
        top: 0;
        z-index: -1;
        transition: $transition;
        background-color: $transparent-clr;
      }
      span:first-of-type {
        display: block;
        font-size: 20px;
        font-weight: bold;
        color: $white;
      }
      .stars {
        .filled {
          color: $yellow;
        }
      }
      span:last-of-type {
        display: block;
        font-size: 18px;
        color: $main-clr;
      }
      a {
        text-transform: capitalize;
        border: 1px solid $main-clr-alt;
        border-radius: 5px;
        padding: 5px 10px;
        color: #fff;
        transition: $transition;
        background-color: $main-clr;
        &:hover {
          background-color: $main-clr-alt;
        }
      }
    }
    &:hover {
      & figcaption {
        display: flex;
        &::before {
          width: 100%;
        }
      }
      & a span::before {
        width: 100%;
      }
    }
  }
}
/* -------- End Features section ------*/
/* ------- Start Products section -----*/
.products {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 50px;
  }
  figure {
    border: 1px solid $main-clr-alt;
    overflow: hidden;
    border-radius: $border-radius;
    figcaption {
      padding: 10px 0 0 10px;
      transition: $transition;
      background-color: #efa38bb5;
      &:hover {
        background-color: #efa38b;
      }
      > span:first-of-type {
        display: block;
        font-size: 20px;
        font-weight: bold;
        color: black;
      }
      .stars {
        .filled {
          color: $yellow;
        }
        .empty {
          color: $gray-txt-clr;
        }
      }
      .details {
        display: flex;
        align-items: center;
        justify-content: space-between;
        span {
          font-size: 18px;
          color: $main-clr;
        }
        a {
          text-transform: capitalize;
          border-top: 1px solid $main-clr-alt;
          border-left: 1px solid $main-clr-alt;
          border-radius: 5px 0 0 0;
          padding: 5px 10px;
          color: #fff;
          transition: $transition;
          background-color: $main-clr;
          &:hover {
            background-color: $main-clr-alt;
          }
        }
      }
    }
  }
}
/* -------- End Products section ------*/

/* ------- Start Footer section -------*/
footer {
  @extend %footer;
  @extend %spacer;
}
/* -------- End Footer section --------*/
